#timer {
	svg {
		display: block;
		margin: 0 auto;
		width: 3.2rem;
		height: 3.2rem;
	}
	.loader, .inner {
		fill: #fff;
	}
	.outer, .start {
		fill: $primary;
		transition: .2s fill linear;
	}
	.completed, .ending {
		fill: $alert;
	}
	p {
		margin-top: .5em;
		font-size: 1.3rem;
		color: #ccc;
	}
}
.sub-section #timer {
	.loader, .inner {
		fill: $contentBoxBackground;
	}
}