form {
    text-align: left;
	@media all and (min-width: $mediumWidth) {
		margin: 4rem auto 0;
		padding: 0 8rem;
		max-width: $mediumWidth;
	}
	input, select {
		padding: .5em;
		width: 100%;
		border: 0;
		border-radius: 3px;
		font-size: 2rem;
	}

    select option:first-child{
        color: $placeholderText;
    }

	input:not([type=submit]), select:not([type=submit]) {
		border: 1px solid #999;
	}
	input[type=submit] {
        margin-top: 2rem;
		color: #fff;
		background-color: #4DAFD9;
	}

    .form-group{
        margin-bottom: 2rem;
    }
}