.page-main section[role=main] {
	padding: 2rem 1.5rem;
	> div {
		box-shadow: 0 0 10px rgba(0,0,0,.1);
		border-radius: $borderRadius;
	}
	@media all and (min-width: $mediumWidth) {
		padding: 0 0 2rem 0;
		> div {
			text-align: left;
			box-shadow: none;
		}
	}
}

.test-areas {
	margin: 0;
	padding: 0;
	list-style: none;
	font-size: 2rem;
	> li {
		margin-top: 2em;
		padding-top: 2em;
		@media all and (min-width: $mediumWidth) {
			padding-top: 0;
			padding-left: 4em;
			background-position: top left;
			h2, p {
				margin: .25em 0;
			}
		}
	}
}