@import 'globals/variables';
@import 'globals/normalize';
@import 'globals/typography';
@import 'globals/buttons';
@import 'globals/icons';
@import 'partials/forms';
@import 'partials/audio';
@import 'partials/modals';
@import 'partials/timer';
@import 'partials/circles';
@import 'partials/meter';

@import 'pages/main';
@import 'pages/tos';
@import 'pages/score';

* { box-sizing: border-box; }

body {
	background-color: #fff;
	@media all and (min-width: $mediumWidth) {
		background-color: $bodyBackground;
	}
}

header[role=banner] {
	padding: 1rem 1.5rem;
	background-color: #fff;
	border-bottom: 1px solid #ccc;
	@media all and (min-width: $mediumWidth) {
		padding: 2rem 1.5rem;
	}
	#logo {
		width: 134px;
	}
}

section[role=main] {
	@media all and (min-width: $mediumWidth) {
		margin: 7rem 1.5rem;
		padding: 0 1.5rem;
		max-width: $mediumWidth + 400px;
		background-color: #fff;
		box-shadow: 0 0 10px rgba(0,0,0,.1);
		border-radius: $borderRadius;
	}
	@media all and (min-width: $mediumWidth + 430px) {
		margin: 7rem auto;
	}

	> div {
		margin-bottom: 2rem;
		padding: 1.5rem;
		text-align: center;
	    color: $darkText;
		@media all and (min-width: $mediumWidth) {
			margin: 0 auto;
			padding: 4rem 1.5rem;
			max-width: $mediumWidth;
		}
	}

	nav {
		margin-top: 10rem;
		> ul {
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: left;
			> li {
				display: inline-block;
				&:last-child {
					float: right;
				}
			}
			&::after {
				content: '';
				display: block;
				clear: both;
			}
		}
	}
}


.divider {
    border-top: 1px solid $lineColor;
}

.test-progress {
	display: flex;
	justify-content: center;
	margin-top: 3rem;
	padding: 3rem 0 2rem;
	.steps {
		list-style: none;
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		margin: 0;
		padding: 0;
		> li {
			display: block;
			margin: .3rem;
			height: 1.25rem;
			width: 1.25rem;
			font-size: 1px;
			color: #fff;
			background: #fff;
			border: 1px solid $primary;
			border-radius: 1rem;
			&.done {
				color: $primary;
				background: $primary;
			}
			&.finish {
				height: 1.5833rem;
				width: 1.4167rem;
				background: url(../imgs/icon-finish.svg) no-repeat;
				background-size: 100%;
				border: 0;
				border-radius: 0;
				overflow: hidden;
				text-indent: 2em;
			}
			@media all and (min-width: $mediumWidth) {
				height: 1.5rem;
				width: 1.5rem;
				&.finish {
					height: 1.9rem;
					width: 1.7rem;
				}
			}
		}
	}
	span {
		display: none;
		@media all and (min-width: $mediumWidth) {
			display: inline;
			margin-left: .5em;
			font-size: 1.6rem;
			color: #999;
		}
	}
}

footer[role=contentinfo] {
	padding: 1rem;
	font-size: 1.4rem;
	text-align: center;
	color: #999;
	background-color: #fff;
	border-top: 1px solid #ccc;

	ul {
		margin: 0;
		padding: 0;
		list-style: none;
		> li {
			display: inline-block;
			margin: 0 1em;
		}
		a {
			display: inline-block;
			padding: .5em;
			color: #999;
		}
	}
	@media all and (min-width: 768px) {
		float: left;
		width: 100%;
		text-align: left;
		p {
			float: left;
		}
		ul {
			float: right;
			margin: 1em 0;
			> li {
				margin: 0 .5em;
				&:first-child {
					margin-right: 0;
					padding-right: .5em;
					border-right: 1px solid;
				}
				> a {
					padding: 0;
				}
			}
		}
	}
}

.hide {
	display: none !important;
}

.sub-section{
    background-color: $contentBoxBackground;
    border-radius: $borderRadius;
    padding: .1rem 2rem;
    max-width: 45rem;
    margin: 5em auto;
    text-align: left;
}

.answer {
    padding: 1rem 2rem;
    margin: 1.5rem auto;
    max-width: 45rem;
    background-color: $contentBoxBackground;
    border-radius: $borderRadius;
    text-align: left;
    cursor: pointer;
    p {
        margin: 0;
    }
    &:hover {
	    background-color: darken($contentBoxBackground, 10%);
	    color: $darkText;
	}

	&.selected,
	&.selected:hover {
	    background-color: darken($contentBoxBackground, 40%);
	    color: #fff;
	}

	&.correct {
		padding-right: calc(28px + 1.5em);
		background: rgba($correct, .25) url(../imgs/icon-check.png) no-repeat right 1em top 1em;
		color: $darkText;
	}
	&.incorrect {
		padding-right: calc(28px + 1.5em);
		background: rgba($incorrect, .25) url(../imgs/icon-cross.png) no-repeat right 1em top 1em;
		color: $darkText;
	}
}

.text-box {
    width: 75%;
    margin: auto;
    resize: none;
    padding: 1.5rem 2rem;
    border: solid 1px darken($contentBoxBackground, 20%);
    border-radius: $borderRadius;
}

.sharing-icons {
	margin: 0;
	padding: 0;

	> li {
		display: inline-block;
		margin: .5rem;
		height: 4rem;
		width: 4rem;
		> a {
			display: block;
			height: 100%;
			text-indent: 150%;
			overflow: hidden;
			background-size: contain;
		}
    }
}
