%control {
	position: relative;
	display: block;
	height: 5rem;
	width: 5rem;
	border: .4rem solid #ccc;
	border-radius: 3rem;
	overflow: hidden;
	text-indent: 5rem;
	cursor: pointer;
}

.player {
	position: relative;
	display: flex;
	align-items: flex-end;
	justify-content: center;
	margin: 3rem 0;
	> .title {
		position: absolute;
		top: -1.5em;
		left: 0;
		margin: 0;
		width: 100%;
		text-align: center;
		font-size: 1.6rem;
		font-family: $headerFontFamily;
		font-weight: bold;
	}
	.controls > span {
		@extend %control;

		&::after {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			margin: -1rem 0 0 -.25rem;
			width: 0;
		}
		&.play::after {
			border: 1rem solid transparent;
			border-left-color: $primary;
		}
		&.pause::after {
			margin: -.75rem 0 0 -.75rem;
			border: .75rem solid $primary;
		}
	}
	.progress {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;
		margin-left: 2rem;
		width: 100%;
		@media all and (min-width: $mediumWidth) {
			width: 50%;
		}
		> progress {
			flex: 1 0 100%;
			-webkit-appearance: none;
			-moz-appearance: none;
			appearance: none;
			height: 1rem;
			border: 0;
			color: $primary;
			&::-webkit-progress-value {
				background: $primary;
			}
			&::-moz-progress-bar {
				background: $primary;
			}
			&::-webkit-progress-bar {
				background: #ccc;
			}
		}
		> .time {
			margin: .25em;
			font-size: 1.6rem;
			color: #999;
		}
	}
	progress {

	}
}
.no-js.player {
	> .controls, > .progress { display: none; }
}

.recorder {
	.timer {
		font-size: 3.6em;
		color: #ccc;
		&.active {
			color: $alert;
		}
	}
	> .controls {
		> span {
			@extend %control;
			margin: 2rem auto 0;
			background-position: center;
			&.stop {
				border-color: rgba($alert, .5);
				animation: record .5s ease-in-out infinite alternate;
			}
		}
	}
	.message {
		margin-top: 1em;
		font-size: 1.8rem;
		color: $primary;
	}
}
@keyframes record {
	0% {
		box-shadow: 0 0 0 .6rem rgba($alert, 0), 0 0 0 1.2rem rgba($alert, 0);
	}
	50% {
		box-shadow: 0 0 0 .6rem rgba($alert, .1), 0 0 0 1.2rem rgba($alert, 0);
	}
	100% {
		box-shadow: 0 0 0 .6rem rgba($alert, .2), 0 0 0 1.2rem rgba($alert, .1);
	}
}