.btn, button {
	display: inline-block;
	padding: .5em 2em;
	font-size: 1.8rem;
	line-height: 1.27;
	font-weight: bold;
	text-decoration: none;
	border: .12em solid $neutral;
	border-radius: .23em;
	background-color: $neutral;
	transition: background-color .1s linear, border-color .1s linear;
	&:hover, &:active {
		text-decoration: underline;
		border-color: darken($neutral, 10%);
		background-color: darken($neutral, 10%);
	}
    &.disabled{
        pointer-events: none;
        opacity: 0.4;
        cursor: default;
    }

    &.btn-large {
    	width: 100%;
	    text-align: center;
    	@media all and (min-width: $mediumWidth) {
    		width: auto;
	    	min-width: 20rem;
	    }
    }
    &.btn-small {
    	padding: .5em .75em;
    }
}

// Make sure a.btn colors from _typography don't override these.
@mixin fixLinkButtonColor($color) {
	@at-root {
		@each $sel in & {
			a#{$sel} {
				color: $color;
				&:hover { color: $color; }
			}
		}
	}
}
// Set border and background colors with adjustments for hover/active
@mixin buttonBackground($color) {
	background-color: $color;
	border-color: $color;
	&:hover, &:active {
		background-color: darken($color, 10%);
		border-color: darken($color, 10%);
	}
}

.btn-prev {
	color: $primary;
	background: #fff;
	@include fixLinkButtonColor($primary);
}
.btn-next, .btn-positive {
	color: #fff;
	@include buttonBackground($primary);
	@include fixLinkButtonColor(#fff);
}
.btn-negative {
	color: #fff;
	@include buttonBackground(#999);
	@include fixLinkButtonColor(#fff);
}
.btn-active {
	color: #fff;
	@include buttonBackground($active);
	@include fixLinkButtonColor(#fff);
}


.confirmation-buttons {
	display: flex;
	flex-flow: row wrap;
	padding-top: 3rem;
	.btn, button {
		flex: 1 0 100%;
		margin: 1rem 0;
		@media all and (min-width: $mediumWidth) {
			flex: 1 0 auto;
			margin: 0 2em;
		}
	}
}