.icon {
	padding-top: 2.5em;
	background-position: top center;
	background-repeat: no-repeat;
	background-size: 2em;

	@media all and (min-width: $mediumWidth) {
		@at-root {
			h1#{&} {
				padding-top: 4em;
				background-size: 2.5em;
			}
		}
	}
	&.icon-clock {
		background-image: url(../imgs/icon-clock.svg);
	}
	&.icon-speech {
		background-image: url(../imgs/icon-speech.svg);
	}
	&.icon-listening {
		background-image: url(../imgs/icon-listening.svg);
	}
	&.icon-conversation {
		background-image: url(../imgs/icon-conversation.svg);
	}
	&.icon-understanding {
		background-image: url(../imgs/icon-understanding.svg);
	}
	&.icon-headset {
		background-image: url(../imgs/icon-headset.svg);
	}
	&.icon-audio {
		background-image: url(../imgs/icon-audio.svg);
	}
	&.icon-allow-microphone {
		background-image: url(../imgs/icon-allow-microphone.svg);
	}
	&.icon-no-microphone {
		background-image: url(../imgs/icon-no-microphone.svg);
	}
	&.icon-microphone {
		background-image: url(../imgs/icon-microphone.svg);
	}
	&.icon-audio-simple {
		background-image: url(../imgs/icon-audio-simple.svg);
	}
	&.icon-no-sound {
		background-image: url(../imgs/icon-no-sound.svg);
	}
	&.icon-speech-sounds {
		background-image: url(../imgs/icon-speech-sounds.svg);
	}
	&.icon-email {
		background-image: url(../imgs/icon-email.svg);
	}
	&.icon-facebook {
		background-image: url(../imgs/icon-facebook.svg);
	}
	&.icon-twitter {
		background-image: url(../imgs/icon-twitter.svg);
	}
	&.icon-linkedin {
		background-image: url(../imgs/icon-linkedin.svg);
	}
	&.icon-mail {
		background-image: url(../imgs/icon-mail.svg);
	}
	&.icon-record-microphone {
		background-image: url(../imgs/icon-record-microphone.svg);
	}
	&.icon-record-stop {
		background-image: url(../imgs/icon-record-stop.svg);
	}
}
h2.icon { margin-top: 2em; }