html {
	font-size: 62.5%;
}
body {
	font-family: $defaultFontFamily;
}

h1, h2, h3, h4, h5, h6, .h {
	font-family: $headerFontFamily;
	font-style: normal;
	font-weight: 700;
}

h1 {
	font-size: 2.2rem;
	@media all and (min-width: $mediumWidth) {
		font-size: 2.6rem;
	}
}
h2 {
	font-size: 1.8rem;
	@media all and (min-width: $mediumWidth) {
		font-size: 2rem;
	}
}
section[role=main]{
    p, textarea, label{
        font-size: 1.8rem;
	    line-height: 1.5;
    }
}

a, a:link, a:visited {
	color: $primary;
	&:hover, &:active {
		color: $secondary;
	}
}

.text-center {
	text-align: center !important;
}

.instructions {
	font-weight: bold;
}

.sub-text {
    color: $greyText;
}