.score-about, .score-summaries, .score-detailed {
	text-align: left;
}

.score-about {
    margin: 5rem 0;
    padding: 3rem 0;
    border-top: solid 1px #ccc;
    border-bottom: solid 1px #ccc;
}

.score-summary {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
    margin-bottom: 3rem;
    padding-bottom: 4rem;
    border-bottom: 1px solid $lineColor;

    @media all and (min-width: $mediumWidth) {
    	flex-flow: row nowrap;
    }

	> .label {
		font-size: 1.5rem;
		line-height: 2.5;
		font-weight: bold;

		@media all and (min-width: $mediumWidth) {
			flex: 1 0 auto;
			line-height: 1;
		}
	}
	> .level {
		font-size: 1.2rem;
		line-height: 3.4;
		text-align: right;
		text-transform: uppercase;
		color: $placeholderText;

		@media all and (min-width: $mediumWidth) {
			line-height: 1.5;
		}
	}
	> .meter {
		flex: 1 0 100%;

		@media all and (min-width: $mediumWidth) {
			flex: 0 0 auto;
			width: 50%;
			padding-left: 1rem;
		}
	}
}

.score-detailed {
	> .tabs {
		display: flex;
		justify-content: space-between;
		padding: 0 .3rem;
		border-bottom: .2rem solid $lineColor;
		@media all and (min-width: $mediumWidth) {
			justify-content: flex-start;
		}
		> .tab {
			display: inline-block;
			flex: 0 0 49%;
			margin-bottom: -.2rem;
			padding: .5em;
			text-align: center;
			font-size: 1.6rem;
			font-weight: bold;
			font-family: $headerFontFamily;
			background-color: lighten($lineColor, 10%);
			border: .2rem solid $lineColor;
			cursor: pointer;
			@media all and (min-width: $mediumWidth) {
				flex: 0 0 auto;
				&:last-child {
					margin-left: .3rem;
				}
			}

			> .score {
				padding: .1em .4em;
				border-radius: $borderRadius;
				&.speaking {
					background-color: $secondary;
				}
				&.listening {
					background-color: $primary;
				}
			}

			&.active {
				background-color: #fff;
				border-bottom: 0;
			}
		}
	}
}
.score-detail {
    margin-bottom: 3rem;
    padding-bottom: 4rem;
    border-bottom: 1px solid $lineColor;
    @media all and (min-width: $mediumWidth) {
    	float: left;
    	width: 100%;
    }
	> .score-summary {
		position: relative;
		flex-flow: column nowrap;
		padding-bottom: 0;
		margin-bottom: 0;
		border: 0;
		@media all and (min-width: $mediumWidth) {
			flex-flow: row wrap;
		}
		.label {
			line-height: .5;
			@media all and (min-width: $mediumWidth) {
				font-size: 1.8rem;
			}
		}
		.level {
			text-align: left;
			line-height: 3;
			@media all and (min-width: $mediumWidth) {
				order: 3;
				width: 100%;
			}
		}
		.info {
			position: absolute;
			top: 0;
			right: 0;
			width: 1.5em;
			font-size: 2em;
			text-align: center;
			font-family: $headerFontFamily;
			color: #d8d8d8;
			border: .3rem solid;
			border-radius: 2em;
			cursor: pointer;
			@media all and (min-width: $mediumWidth) {
				top: -.25em;
				right: 51%;
			}
		}
	}

	@media all and (min-width: $mediumWidth) {
		.explaination {
			float: left;
			width: 50%;
		}
		.example {
			padding: .5em 1.5em;
			background-color: rgba($primary, .1);
			border-radius: $borderRadius;
		}
		.samples {
			float: right;
			width: 50%;
		}
	}

	.sounds {
		margin: 0;
		padding: 0;
		list-style: none;
		> li {
			display: inline-block;
			margin: 0 .25em .5em 0;
		}
	}
	.sound-example {
		padding: .5em .75em;
		font-size: 1.6rem;
		line-height: 1.3;
		font-weight: bold;
		font-family: $headerFontFamily;
		background-color: $neutral;
		border-radius: $borderRadius;
		@media all and (min-width: $mediumWidth) {
			font-weight: normal;
			font-family: $defaultFontFamily;
		}
		.active {
			color: $active;
			text-decoration: underline;
		}
	}
	.samples .player {
		margin: 6rem 0 3rem;
		@media all and (min-width: $mediumWidth) {
			margin: 4rem 0 3rem;
		}
	}
	.vocab-example {
		clear: both;
		padding: .5em 1.5em;
		background-color: rgba($primary, .1);
		border-radius: $borderRadius;
		@media all and (min-width: $mediumWidth) {
			columns: 2;
		}
		.active {
			color: $active;
			text-decoration: underline;
		}
	}
}

@media all and (min-width: $mediumWidth) {
	.collapsibles {
		clear: both;
		.collapsibles-group {
			float: left;
			width: calc(50% - 1rem);
			margin-right: 2rem;
			&:last-child {
				margin-right: 0;
			}
		}
	}
}
.collapsible {
	margin: 1em 0;
	> header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: .5em .75em;
		font-size: 1.8rem;
		background-color: $neutral;
		border-radius: $borderRadius;
		cursor: pointer;
		> .title {
			font-weight: bold;
			font-family: $headerFontFamily;
			&::before {
				content: '\2227';
				margin-right: 1em;
				font-size: 1em;
			}
		}
		.result {
			width: 28px;
			height: 28px;
		}
	}

	> .body {
		padding: 1em;
	    margin: -.4rem 0 1rem;
	    border: .4rem solid #eee;
	    border-radius: 0 0 $borderRadius $borderRadius;
	}

	&.correct {
		> header {
			background-color: hsl(hue($correct), saturation($correct), 90%);
			.result {
				background: $correct url(../imgs/icon-check.png) no-repeat;
			}
		}
		> .body {
			border-color: hsl(hue($correct), saturation($correct), 90%);
		}
	}
	&.incorrect {
		> header {
			background-color: hsl(hue($incorrect), saturation($incorrect), 90%);
			.result {
				background: $incorrect url(../imgs/icon-cross.png) no-repeat;
			}
		}
		> .body {
			border-color: hsl(hue($incorrect), saturation($incorrect), 90%);
		}
	}

	&.collapsed {
		> header > .title::before {
			content: '\2228';
		}
		> .body {
			display: none;
		}
	}
}