.meter {
    display: flex;
    align-items: center;
    > span {
        display: block;
        height: 1.25em;
        width: 25%;
        background-color: #d8d8d8;

        &:first-child {
            border-radius: .4rem 0 0 .4rem;
        }
        &:last-child {
            border-radius: 0 .4rem .4rem 0;
        }
        &:not(:first-child) {
            margin-left: .2rem;
        }

        &.active {
            background-color: #2f70a1;
        }
    }
}
