﻿.modal-dialog {
	position: fixed;
    display: flex;
    align-items: flex-start; // use JS to set to center if modal is shorter than viewport
    justify-content: center;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
    padding: 1.5rem;
	background: rgba(0,0,0,0.5);
	opacity:0;
	transition: opacity 400ms ease-in;
    text-align: left;
    z-index: 9999;
    pointer-events: none;
    overflow: auto;
}

.modal-dialog:target {
	opacity:1;
    pointer-events: auto;
}

.modal-dialog > div {
	max-width: 62rem;
	margin: 0 auto;
    padding: 2rem 1.5rem;
    border-radius: $borderRadius;
    background: #fff;
    @media all and (min-width: $mediumWidth) {
        padding: 3rem 7rem;
    }

    p {
        font-size: 2rem;
    }

    h1.icon {
        margin-top: 0;
        text-align: center;
        background-size: 1em;
        @media all and (min-width: $mediumWidth) {
            margin-left: -2em;
            padding: .25em 0 .25em 2em;
            text-align: left;
            background-position: center left;
        }
    }

    ol {
        margin: 0;
        padding-left: 0;
        list-style-position: inside;
        font-size: 2rem;
        li {
            margin-bottom: 1em;
        }
    }

    .example {
        padding: .5em 1em;
        font-weight: bold;
        font-family: $headerFontFamily;
        background-color: $neutral;
        border-radius: $borderRadius;
    }
}