/* Breakpoints */
$smallWidth: 400px;
$mediumWidth: 600px;

/* Colors */
$primary: #4DAFD9;
$secondary: #F4C271;
$alert: #E74F29;
$active: #2f70a1;
$neutral: #eee;
$correct: #4fab67;
$incorrect: #f54c23;

$darkText: #333;
$greyText: #717171;

$bodyBackground: #EFEFEF;
$contentBoxBackground: #F4F4F4;

$placeholderText: #A9A9A9;

$lineColor: #c8c4c4;

$borderRadius: .5rem;

/* Fonts */
$defaultFontFamily: helvetica, arial, sans-serif;
$headerFontFamily: "Gotham Rounded A", "Gotham Rounded B", sans-serif;