.score-circles {
    display: flex;
    //flex-flow: column nowrap;
    flex-flow: row wrap;
    justify-content: space-between;

    // @media all and (min-width: $smallWidth) {
    //     flex-flow: row wrap;
    // }
    @media all and (min-width: $mediumWidth) {
    	flex-flow: row nowrap;
    }

    p {
        font-weight: bold;
    }
    .circles-total p {
        font-size: 2.2rem;
    }

    > div:not(.circles-total) {
        flex: 0 1 50%;
        @media all and (min-width: $mediumWidth) {
            flex: initial;
            margin-top: 30px;
        }
    }
}

.circles-valueStroke {
	stroke-linecap: round;
}
.circles-total {
	position: relative;
    flex-basis:  100%;
    @media all and (min-width: $mediumWidth) {
        flex-basis: auto;
    }
	.circles-text { display: none; }
	> #circle-inner {
		position: absolute;
		top: 20px;
		left: 50%;
		margin-left: -50px; /* match radius in zcircles.js */

    	@media all and (min-width: $mediumWidth) {
    		left: 20px;
    		margin-left: 0;
    	}
		.circles-text { display: block; }
	}}